@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&family=Inconsolata:wght@200..900&display=swap');

@font-face {
  font-family: 'slick';
  font-weight: normal;
  font-style: normal;

  src: url('../node_modules/slick-carousel/slick/fonts/slick.eot');
  src: url('../node_modules/slick-carousel/slick/fonts/slick.eot?#iefix') format('embedded-opentype'), url('../node_modules/slick-carousel/slick/fonts/slick.woff') format('woff'), url('../node_modules/slick-carousel/slick/fonts/slick.ttf') format('truetype'), url('../node_modules/slick-carousel/slick/fonts/slick.svg#slick') format('svg');
}

body {
  margin: 0;
  font-family: "IBM Plex Mono", sans-serif;
  padding-top: 20px;
  background-color: var(--white);
}

a {
  text-decoration: none;
  color: var(--black);
}

.container {
  max-width: 1600px !important;
  width: 90% !important;
  margin-right: auto;
  margin-left: auto;
}

.row {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.column {
  display: flex;
  flex-direction: column;

}

.flex-start {
  justify-content: flex-start;
}

.gap-20 {
  gap: 20px;
}

.gap-40 {
  gap: 40px;
}

.gap-80 {
  gap: 80px;
}

.gap-25 {
  gap: 25px;
}

.gap-150 {
  gap: 150px;
}


/* Colors */
:root {
  --white: #ffffff;
  --black: #000000;
  --purple: #A8A6FF;
  --pink: #FFA6F6;
  --blue: #A6FAFF;
  --green: #B8FF9F;
  --yellow: #FFF59F;
  --orange: #FFC29F;
  --red: #FF9F9F;
}

/* Background */
.bg-white {
  background-color: var(--white);
}

.bg-black {
  background-color: var(--black);
}

.bg-purple {
  background-color: var(--purple);
}

.bg-pink {
  background-color: var(--pink);
}

.bg-blue {
  background-color: var(--blue);
}

.bg-green {
  background-color: var(--green);
}

.bg-yellow {
  background-color: var(--yellow);
}

.bg-orange {
  background-color: var(--orange);
}

.bg-red {
  background-color: var(--red);
}

/* hovers */
.hover-white:hover {
  background-color: var(--white);
}

.hover-black:hover {
  background-color: var(--black);
}

.hover-purple:hover {
  background-color: var(--purple);
}

.hover-pink:hover {
  background-color: var(--pink);
}

.hover-blue:hover {
  background-color: var(--blue);
}

.hover-green:hover {
  background-color: var(--green);
}

.hover-yellow:hover {
  background-color: var(--yellow);
}

.hover-orange:hover {
  background-color: var(--orange);
}

.hover-red:hover {
  background-color: var(--red);
}

/* Text */
.text-white {
  color: var(--white);
}

.text-black {
  color: var(--black);
}

.text-purple {
  color: var(--purple);
}

.text-pink {
  color: var(--pink);
}

.text-blue {
  color: var(--blue);
}

.text-green {
  color: var(--green);
}

.text-yellow {
  color: var(--yellow);
}

.text-orange {
  color: var(--orange);
}

.text-red {
  color: var(--red);
}

.title {
  font-weight: 700;
  line-height: normal;
}

.xlarge-title {
  font-size: 202px;
}

.large-title {
  font-size: 92px;
}

.medium-title {
  font-size: 64px;
}

.small-title {
  font-size: 48px;
}


.content {
  font-weight: 400;
  line-height: normal;
}

.large-content {
  font-size: 32px;
}

.medium-content {
  font-size: 18px;
}

.small-content {
  font-size: 14px;
}

.xsmall-content {
  font-size: 14px;
}

.badge {
  font-weight: 400;
  line-height: normal;
}

.bold-badge {
  font-weight: 700;
}


.small-badge {
  font-size: 8px;
}

/* Borders */
.border2 {
  border: 2px solid var(--black);
}

.border4 {
  border: 4px solid var(--black);
}

.border8 {
  border: 8px solid var(--black);
}

/* Shadows */
.shadow2 {
  box-shadow: 2px 2px 0px 0px var(--black);
}

.shadow4 {
  box-shadow: 4px 4px 0px 0px var(--black);
}

.shadow8 {
  box-shadow: 8px 8px 0px 0px var(--black);
}

.shadow12 {
  box-shadow: 12px 12px 0px 0px var(--black);
}

/* Box hovers */
.hover-shadow2:hover {
  box-shadow: 2px 2px 0px 0px var(--black);
}

.hover-shadow4:hover {
  box-shadow: 4px 4px 0px 0px var(--black);
}

.hover-shadow8:hover {
  box-shadow: 8px 8px 0px 0px var(--black);
}

.hover-shadow12:hover {
  box-shadow: 12px 12px 0px 0px var(--black);
}

.hover-white:hover {
  background-color: var(--white);
}

.hover-black:hover {
  background-color: var(--black);
}

.hover-purple:hover {
  background-color: var(--purple);
}

.hover-pink:hover {
  background-color: var(--pink);
}

.hover-blue:hover {
  background-color: var(--blue);
}

.hover-green:hover {
  background-color: var(--green);
}

.hover-yellow:hover {
  background-color: var(--yellow);
}

.hover-orange:hover {
  background-color: var(--orange);
}

.hover-red:hover {
  background-color: var(--red);
}

/* Buttons */
.button {
  display: inline-flex;
  height: 50px;
  min-width: 150px;
  padding: 10px 20px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  flex-shrink: 0;
  border: 4px solid var(--black);
  color: var(--black);
  text-align: center;
  font-family: "IBM Plex Mono";
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  text-transform: uppercase;
  
}

.button:hover {
  box-shadow: 8px 8px 0px 0px var(--black);
}

.button_bigger {
  min-width: 300px;
  height: 75px;
  padding: 15px 30px;
}


.badge {
  display: inline-flex;
  height: 25px;
  min-width: 75px;
  padding: 5px 10px;
  justify-content: center;
  align-items: center;
  border: 4px solid var(--black);
  color: var(--black);
  text-align: center;
  font-weight: 700;
  line-height: normal;
  text-transform: uppercase;

}

.project {
  border: 4px solid var(--black);
  padding: 20px;
  min-height: 500px !important;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

}

.project-content {

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.project-badges {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 20px;
  margin-top: 20px;
  margin-bottom: 20px;

}

.work {
  border: 4px solid var(--black);
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 20px;
}

.resume-partname {
  margin-bottom: 20px;
}

.projects-swiper {
  margin-bottom: 109px !important;
}

.single-technology {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
  border: 4px solid var(--black);
  padding: 5px 20px;
  max-height: 35px;
}

.technology-starsspace {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
}

.contact-form {
  width: 100%;
  margin-top: 30px;
}

.contact-form label {
  font-size: 18px;
  font-weight: 700;
  line-height: normal;
  text-transform: uppercase;
  display: block;
}

.contact-input {
  padding: 20px 0;
  margin-bottom: 20px;
  border: 4px solid var(--black);
  box-sizing: border-box;
  height: 20px;
  font-size: 24px;
  font-family: 'IBM Plex Mono', monospace;
  border-radius: 0px;
}
.formspace{
  width: 50%;
}

.contact-input:focus {
  outline: none;
  box-shadow: 8px 8px 0px 0px var(--black);
}

.social{
  width: 50%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  margin-top: 20px;

}
.contact-message {
  margin-bottom: 20px;
  border: 4px solid var(--black);
  height: 200px;
  resize: none;
  align-content: flex-start;

}

.contact-submit {
  width: 20% !important;
  padding: 10px;
  border: 4px solid var(--black);
  background-color: var(--white);
  color: var(--black);
  font-size: 18px;
  font-weight: 700;
  line-height: normal;
  text-transform: uppercase;
  cursor: pointer;
  margin-top: 10px;
  align-self: flex-end;
  border-radius: 0px;
}

.resume {
  align-items: start;
}

.social-buttonspace {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}

.social-buttonspace-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.hero-title {
  margin-top: 5%;
  font-size: 20vw !important;
}

.subpage-title {
  margin-top: 20px;
}

.hero-title__2row {
  margin-top: -7%;
  display: block;
  margin-left: 160px
}

.nav {
  margin-top: 7%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  @media (max-width: 768px) {
    gap: 20px !important;
    
  }
}

.submenu {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.contentspace {
  margin-top: 20px;
  margin-bottom: 40px;
}

.swiper-button-prev,
.swiper-button-next {
  color: var(--black) !important;
  border: 4px solid var(--black);
  background-color: var(--white);
  width: 50px !important;
  height: 50px !important;
}

.swiper-button-prev::after,
.swiper-button-next::after {
  font-size: 30px !important;
}

.swiper-button-prev:hover,
.swiper-button-next:hover {
  box-shadow: 4px 4px 0px 0px var(--black);
}

/* for screens < 768px */
@media (max-width: 768px) {
  .sm-col{
    display: flex;
    flex-direction: column !important;
  }
  .contact-input {
    width: 100%;
  }
  .formspace{
    width: 100%;
    margin-bottom: 80px;
  }
  .sm-none{
    display: none;
  }
  .gap-150{
    gap: 40px;
  }
  .xlarge-title {
    font-size: 94px;
    line-height: 150%;
  }
  .large-title {
    font-size: 54px;
  }
  .medium-title {
    font-size: 42px;
  }
  .small-title {
    font-size: 32px;
  }
  
  .hero-title__2row {
    margin-top: -50px;
    display: block;
    margin-left: 0px
  }
}